@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Playfair+Display&display=swap);
/* navbar */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 40px 50px;
}
.logo {
  font-family: "Playfair Display", serif;
  color: #000;
  text-decoration: none;
  font-size: 1.8rem;
  font-weight: bold;
  margin-right: 50px;
}

/* serchbar */
.search__bar {
  display: flex;
  align-items: center;
  flex: 1 1;
}
.search__input {
  height: 46px;
  padding: 15px;
  width: 100%;
  font-size: 1rem;
  outline: none !important;
  background-color: #eeeeee;
  border: none;
}
.search__input::-webkit-input-placeholder {
  font-size: 0.9rem;
}
.search__input:-ms-input-placeholder {
  font-size: 0.9rem;
}
.search__input::placeholder {
  font-size: 0.9rem;
}
.search__icon {
  background-color: #000;
  color: #fff;
  padding: 12px 20px;
  font-size: 1rem;
  cursor: pointer;
  border: none;
  outline: none;
  transition: 0.3s;
}
.search__icon:hover {
  background-color: #000;
}

/* nav links */
.nav__list {
  display: flex;
}
.nav__item {
  list-style: none;
  margin-left: 50px;
}
.nav__link {
  color: gray;
  font-size: 1rem;
  text-decoration: none;
}
.nav__item:hover .nav__link {
  color: #000;
}
.nav__active {
  color: #000;
  font-weight: bold;
}

/* nav icon */
.nav__icon {
  display: none;
  margin-left: 50px;
  cursor: pointer;
}

/* screen size 768px */
@media only screen and (max-width: 768px) {
  /* navbar */
  .logo {
    font-size: 1.6rem;
  }

  /* nav links */
  .nav__list {
    position: fixed;
    width: 50%;
    height: 100vh;
    top: 0;
    left: -100%;
    flex-direction: column;
    box-shadow: 10px 0 30px rgba(0, 0, 0, 0.1);
    align-items: center;
    justify-content: center;
    background-color: #fff;
    z-index: 2;
    transition: 0.5s;
  }
  .nav__item {
    margin: 35px 0;
  }
  .nav__link {
    font-size: 1.2rem;
  }

  /* nav links active */
  .nav__list__active {
    left: 0;
  }

  /* nav icon */
  .nav__icon {
    display: block;
  }
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  /* navbar */
  .header {
    padding: 30px 40px;
  }
  .logo {
    font-size: 1.4rem;
  }
  /* serchbar */
  .search__bar {
    display: none;
  }
  /* nav links */
  .nav__list {
    width: 70%;
  }
}

.banner {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 50px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}
.banner__heading {
  color: #000;
  font-size: 3rem;
  width: 40%;
  font-family: "Playfair Display", serif;
  line-height: 1.5;
}
.banner__img {
  width: 40%;
  height: 350px;
}

/* screen size 1024px */
@media only screen and (max-width: 1024px) {
  .banner__heading {
    width: 50%;
    font-size: 2.7rem;
  }
  .banner__img {
    width: 35%;
  }
}

/* screen size 768px */
@media only screen and (max-width: 768px) {
  .banner__heading {
    width: 55%;
    font-size: 2.2rem;
  }
  .banner__img {
    width: 35%;
  }
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  .banner {
    padding: 80px 0;
    text-align: center;
  }
  .banner__heading {
    width: 75%;
    font-size: 2rem;
  }
  .banner__img {
    display: none;
  }
}

/* screen size 360px */
@media only screen and (max-width: 360px) {
  .banner__heading {
    width: 90%;
    font-size: 1.8rem;
  }
}

.sidebar__detail {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid #cccccc;
}
.sidebar__detail__img {
  width: auto;
  height: 50px;
  object-fit: contain;
  border: 1px solid;
}
.sidebar__detail__heading {
  font-size: 1rem;
  margin-left: 20px;
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  .sidebar__detail {
    align-items: center;
    justify-content: center;
  }
  .sidebar__detail__heading {
    font-size: 0.9rem;
  }
}

/* weather section */
.weather__section {
  width: 30%;
  border: 2px solid;
}
.weather__heading {
  text-align: center;
  padding: 10px;
  font-size: 1.3rem;
  background-color: #000;
  color: #fff;
}

/* weather detail */
.weather__detail__section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 40px;
  padding: 0 30px 30px;
}
.weather__detail p:nth-child(1) {
  font-size: 1.2rem;
  font-weight: bold;
}
.weather__detail p:nth-child(2) {
  font-size: 2.3rem;
  font-weight: bold;
  margin: 25px 0 40px;
  width: 50%;
}
.weather__detail p:nth-child(3) {
  font-size: 1rem;
}
.weather__detail__section img {
  width: 80px;
}

/* screen size 1024px */
@media only screen and (max-width: 1024px) {
  /* weather detail */
  .weather__detail p:nth-child(1) {
    font-size: 1.1rem;
  }
  .weather__detail p:nth-child(2) {
    font-size: 2rem;
  }
}

/* screen size 768px */
@media only screen and (max-width: 768px) {
  /* weather section */
  .weather__section {
    width: 50%;
    margin: 70px auto 30px;
  }
  /* weather detail */
  .weather__detail p:nth-child(1) {
    font-size: 1rem;
  }
  .weather__detail p:nth-child(2) {
    font-size: 1.8rem;
    margin: 30px 0;
  }
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  /* weather section */
  .weather__section {
    width: 90%;
    margin: 70px 0 30px;
  }
  .weather__heading {
    font-size: 1.2rem;
  }

  .weather__detail p:nth-child(2) {
    font-size: 1.6rem;
    margin: 25px 0;
  }
}

.news__card {
  width: 40%;
  margin: 50px 0;
  overflow: hidden;
  cursor: pointer;
}
.news__card > img {
  width: 100%;
  transition: 0.3s;
}
.news__card > .news__detail > h1 {
  font-size: 1.5rem;
  margin: 20px 0 25px;
  font-family: "Playfair Display", serif;
}

/* screen size 768px */
@media only screen and (max-width: 768px) {
  .news__card {
    width: 45%;
    margin: 30px 0;
  }
  .news__card > .news__detail > h1 {
    font-size: 1.3rem;
  }
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  .news__card {
    width: 75%;
  }
}

/* screen size 360px */
@media only screen and (max-width: 360px) {
  .news__card {
    width: 90%;
    margin: 25px 0;
  }
  .news__card > .news__detail > h1 {
    font-size: 1.1rem;
  }
}

.footer {
  text-align: center;
  padding: 50px 0 30px;
  background-color: #eeeeee;
}
.footer > .social__media__link > a {
  margin: 0 15px;
  color: #000;
  opacity: 0.5;
  transition: 0.3s;
}
.footer > .social__media__link > a:hover {
  opacity: 1;
}
.footer > p {
  font-size: 0.9rem;
  word-spacing: 2px;
  margin-top: 25px;
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  .footer {
    padding: 35px 0;
  }
  .footer > p {
    font-size: 0.8rem;
    margin-top: 15px;
  }
}

/* screen size 360px */
@media only screen and (max-width: 360px) {
  .footer > .social__media__link > a {
    margin: 0 10px;
  }
}

.full__news > img {
  width: 100%;
  margin: 60px 0;
}
.full__news > p {
  font-size: 1.1rem;
}
.full__news > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 70px;
}
.full__news > div > p {
  font-size: 0.9rem;
  font-weight: bold;
}

/* screen size 1024px */
@media only screen and (max-width: 1024px) {
  .full__news > h1 {
    font-size: 1.8rem;
  }
}

/* screen size 768px */
@media only screen and (max-width: 768px) {
  .full__news > h1 {
    font-size: 1.5rem;
  }
  .full__news > p {
    font-size: 1rem;
  }
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  .full__news > h1 {
    font-size: 1.3rem;
  }
  .full__news > p {
    font-size: 1rem;
  }
  .full__news > div {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 50px;
  }
  .full__news > div > p {
    font-size: 0.8rem;
    font-weight: bold;
    margin: 5px 0;
  }
}

/* sidebar - weather */
.sidebar__weather__section {
  margin: 8% 0;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
}
.sidebar__news {
  display: flex;
  flex-direction: column;
  width: 40%;
}
.sidebar__news__heading {
  font-size: 1.4rem;
  margin-bottom: 30px;
  color: #000;
}

/* carousel */
.carousel {
  width: 75%;
  margin: 80px auto;
  position: relative;
  height: 250px;
}
.carousel__detail > .carousel__link > img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  object-position: center 30%;
}
.carousel__detail > .carousel__link > h1 {
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translateX(-50%);
  width: 70%;
  font-size: 1.5rem;
  color: #fff;
}

/* screen size 768px */
@media only screen and (max-width: 768px) {
  /* sidebar - weather */
  .sidebar__weather__section {
    margin: 8%;
    flex-direction: column;
  }
  .sidebar__news {
    width: 70%;
    margin: auto;
  }

  /* carousel */
  .carousel {
    width: 80%;
    height: 200px;
  }
  .carousel__detail > .carousel__link > img {
    height: 200px;
  }
  .carousel__detail > .carousel__link > h1 {
    font-size: 1.2rem;
  }
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  /* sidebar - weather */
  .sidebar__news {
    width: 90%;
    margin: 0;
  }
  .sidebar__news__heading {
    font-size: 1.2rem;
    margin: 20px 0;
  }
  /* carousel */
  .carousel {
    width: 90%;
    height: 150px;
    margin: 60px auto;
  }
  .carousel__detail > .carousel__link > img {
    height: 150px;
  }
  .carousel__detail > .carousel__link > h1 {
    font-size: 1rem;
  }
}

/* screen size 360px */
@media only screen and (max-width: 360px) {
  /* carousel */
  .carousel {
    width: 100%;
  }
  .carousel__detail > .carousel__link > h1 {
    font-size: 0.9rem;
  }
}

/* import font style */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

/* section */
.section__width {
  width: 100%;
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
}

/* news section */
.news__section {
  width: 90%;
  margin: 8% auto 15%;
}
.news__section__heading {
  font-size: 2.3rem;
  word-spacing: 5px;
  font-family: "Playfair Display", serif;
  text-align: center;
  padding: 10px;
  border-top: 2px solid;
  border-bottom: 2px solid;
  margin-bottom: 50px;
}

/* main news section */
.main__news__section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

/* screen size 1024px */
@media only screen and (max-width: 1024px) {
  /* news section */
  .news__section__heading {
    font-size: 2rem;
  }
}

/* screen size 768px */
@media only screen and (max-width: 768px) {
  /* news section */
  .news__section__heading {
    font-size: 1.8rem;
  }

  /* main news section */
  .main__news__section {
    justify-content: space-around;
  }
}

/* screen size 480px */
@media only screen and (max-width: 480px) {
  /* news section */
  .news__section__heading {
    font-size: 1.5rem;
  }
  /* main news section */
  .main__news__section {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

/* screen size 360px */
@media only screen and (max-width: 360px) {
  /* news section */
  .news__section__heading {
    font-size: 1.3rem;
    margin-bottom: 30px;
  }
}

